<template>
  <div>
    <v-row align="center" justify="center">
      <v-col cols="6" sm="8" md="4">
        <v-card class="elevation-12" raised outlined>
          <v-toolbar color="primary" dark>
            <v-toolbar-title>School Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                label="Login"
                name="login"
                prepend-icon="mdi-account"
                type="text"
                v-model="schoolEmail"
                @keydown.enter="LoginSchool"
              ></v-text-field>
              <v-text-field
                label="Password"
                name="password"
                prepend-icon="mdi-lock"
                v-model="schoolPassword"
                type="password"
                @keydown.enter="LoginSchool"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="LoginSchool">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col cols="1" class="text-center">
        <em>or...</em>
      </v-col>
      <v-col cols="6" sm="8" md="4">
        <v-card class="elevation-12" raised outlined>
          <v-toolbar color="accent" dark>
            <v-toolbar-title>Teacher Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                label="Login"
                name="login"
                prepend-icon="mdi-account"
                type="text"
                @keydown.enter="LoginTeacher"
                v-model="teacherId"
              ></v-text-field>

              <v-text-field
                label="Password"
                name="password"
                prepend-icon="mdi-lock"
                type="password"
                @keydown.enter="LoginTeacher"
                v-model="teacherPassword"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="accent">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import TeacherService from "../services/TeacherService";
export default {
  data: function() {
    return {
      schoolEmail: "",
      schoolPassword: "",
      teacherId: "",
      teacherPassword: ""
    };
  },
  methods: {
    LoginTeacher() {
      TeacherService.LoginTeacher(this.teacherId, this.teacherPassword).then(
        response => {
          this.$store.commit("setLoggedAs", "teacher");
          this.$store.commit("setToken", response.token);
          this.$router.push("Home");
        }
      );
    },

    LoginSchool() {
      SchoolService.LoginSchool(this.schoolEmail, this.schoolPassword).then(
        response => {
          this.$store.commit("setLoggedAs", "school");
          this.$store.commit("setToken", response.token);
          this.$router.push("Home");
        }
      );
    }
  }
};
</script>

