import axios from 'axios'

const url = '/api/teachers'

class TeacherService {
    static async CreateTeacher(teacherBody) {
        const response = await axios.post(url, { teacherBody })
        return response.data
    }

    static async GetTeacherById(teacherId) {
        const response = await axios.get(`${url}/${teacherId}`);
        return response.data
    }

    static async DeleteTeacher(teacherId) {
        const response = await axios.delete(`${url}/${teacherId}`)
        return response.data;
    }

    static async LoginTeacher(teacherId, password) {
        const response = await axios.post(`/api/login/teacher`, { teacherId, password })
        return response.data;
    }

    static async GetStudents(teacherId) {
        const response = await axios.get(`${url}/${teacherId}/students`)
        return response.data;
    }

}

export default TeacherService;