import axios from 'axios'

const url = '/api/schools/'

class SchoolService {
    static async Restart() {
        const response = await axios.post('/api/test/restart')
        return response.data
    }

    static async GetHomeworks(schoolId) {
        const response = await axios.get(url + schoolId + '/homeworks')
        return response.data
    }

    static async CreateSchool(schoolBody) {
        const response = await axios.post(url, schoolBody)
        return response.data
    }

    static async DeleteSchool(schoolId) {
        const response = await axios.delete(url + schoolId)
        return response.data
    }

    static async LoginSchool(email, password) {
        const response = await axios.post(`/api/login/school`, { email, password })
        return response.data;
    }

    static async GetTeachers() {
        const response = await axios.get(`${url}teachers`);
        return response.data;
    }
}

export default SchoolService;